* {
  box-sizing: border-box;

}

body {
  margin: 0;
  background: #0B2434;
  padding: 20px;
  font-family: 'Karla', sans-serif;
}

main {
  background-color: #F5F5F5;
  height: 400px;
  max-width: 800px;
  border-radius: 10px;
  margin: auto;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  
  cursor: pointer;
}
.title {
  font-size: 40px;
  margin: 0;
}

.instructions {
  font-family: 'Inter', sans-serif;
  font-weight: 400;
  margin-top: 0;
  text-align: center;
}
.dice-container {
  display: grid;
  grid-template: auto auto / repeat(5, 1fr);
  gap: 20px;
  margin-bottom: 40px;
}

.die-face {
  height: 50px;
  width: 50px;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
}

.die-num {
  font-size: 2rem;
}
.roll-dice{
  width: 150px;
  height: 40px;
  border:none;
  border-radius: 5px;
  background: #5035FF;
  box-shadow: 0px 3.2px 10px rgba(0, 0, 0, 0.18);
  font-size: 20px;
  font-family: 'Karla', sans-serif;
  color: white;
  cursor: pointer;
 
  
}
.roll-dice:focus{
  outline:none;
}
.roll-dice:active{
  box-shadow: inset 5px 5px 10px -3px rgba(0, 0, 0, 0.7);
}

.rollCount{
  align-self: flex-start;
}